@import url(https://fonts.googleapis.com/css?family=Lato:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
header{position:sticky;top:0;left:0;width:100%;background-color:#fff;box-shadow:5px 2px 30px grey}nav{height:100px;position:relative;display:flex;justify-content:space-between;align-items:center}nav .nav-logo{margin-left:1rem;width:75px;height:75px;max-width:100%;height:auto}nav .nav-toggler{margin-right:2rem;width:25px;height:25px;display:block;cursor:pointer;color:#000;text-align:center}nav .nav-toggler i{font-size:2em}nav .nav-list{display:flex;padding:1.5em;list-style:none}nav .nav-list li{padding:1em;color:#000}nav .nav-list li a .fab{font-size:1.5em}nav .nav-link{position:relative;text-decoration:none;font-style:italic;color:#000;text-decoration:none}nav .nav-link:hover:after{border-color:#ffcd33;right:0}nav .nav-link:after{border-radius:1em;border-top:.2em solid #fff;content:"";position:absolute;right:100%;bottom:-.5em;left:0;transition:right 0.4s cubic-bezier(0, 0.5, 0, 1),border-color 0.4s ease-out}@media screen and (max-width: 960px){.nav-collapse{text-align:center;width:100%;position:absolute;top:100px;left:-100px;opacity:0;background-color:#fff3cc;transition:all 0.5s ease}.nav-collapse .nav-list{display:flex;flex-direction:column}.nav-collapse .nav-list li a{display:none}.nav-collapse.active{left:0;opacity:1;transition:all 0.5s ease;z-index:1}.nav-collapse.active .nav-list li a{display:block;width:100%}}

footer{padding:3em 1em;background-color:#ffcd33;color:#fff;width:100%;min-height:100px;overflow:hidden}footer .footer-copyright{text-align:center;margin-top:3em;font-size:0.8rem}footer .footer-link{font-size:2em;color:#fff}


html,body{box-sizing:border-box;margin:0;padding:0px}section{padding:3em 1em}.container{max-width:1200px;margin:auto}body{font-family:'Lato', sans-serif}a,Link{text-decoration:none;font-style:italic;cursor:pointer}h1{display:block;font-size:3rem;padding:1em 1em}h2{font-size:1.5rem}h3{text-transform:uppercase;font-size:2rem;font-weight:bold;margin:1em 0}h4{text-transform:uppercase;font-size:1rem;font-weight:bold;margin:0}h5{font-size:1rem;color:#ffcd33}h6{font-size:1rem;margin:0.5em}p{font-family:'Open Sans', sans-serif}img{max-width:100%;height:auto}.primary{color:#ffcd33}.black{color:#000}.white{color:#fff}

.intro{color:#fff;background-image:no-repeat;background-size:cover;background-attachment:fixed;height:calc(50vh - 80px);min-height:300px;text-align:center;font-weight:bold;display:flex;justify-content:center;align-items:center;max-width:100%}.intro-high{color:#000;height:calc(70vh - 80px)}

.home-cta{background-color:#ffcd33;color:#fff}

.iconfields{color:#ffcd33}.iconfield{display:flex;flex-direction:column;justify-content:space-between;margin:1em 0}.iconfield .iconfield-wrap{display:flex;flex-direction:column}.iconfield .iconfield-wrap .iconfield-header{display:flex;margin:1em 0}.iconfield .iconfield-wrap .iconfield-header .iconfield-icon{font-size:1.5rem;margin-left:0;margin-right:1em}.iconfield p{color:#000;text-align:justify;margin:1em 0}.iconfield a{color:#ffcd33}.iconfield .iconfield-link-wrapper{display:block}

.btn{display:inline-block;padding:.75em 1.5em;margin-top:1em;border:.2em solid;text-decoration:none;font-weight:300;text-align:center;transition:all .25s;font-style:normal;border-radius:10px;cursor:pointer}.btn-primary{background-color:#ffcd33;border-color:#fff;color:#fff}.btn-primary:hover{background-color:#fff;color:#000}.btn-secondary{background-color:#ffcd33;border-color:#fff;color:#fff}.btn-secondary:hover{background-color:transparent}

.event-cta{background-image:no-repeat;background-size:cover;text-align:center;max-width:100%;color:#fff}

.register-cta{background-color:#ffcd33;text-align:center;color:#fff !important}.register-description-wrapper{margin:2em 0}.register-description-wrapper a,.register-description-wrapper Link{color:#ffcd33}

.faq-field{margin:2em 0}.faq-question::after{content:"+";float:right;padding-right:0.5em}.faq-question:hover{cursor:pointer}.faq-question.active::after{content:"-"}.faq-hr{border-top:2px solid #ffcd33;margin:.5em 0;width:100%}.faq-hide{display:none;-webkit-transform-origin:left top;transform-origin:left top;-webkit-transform:scaleY(0);transform:scaleY(0)}.faq-hide.active{display:block;-webkit-transform-origin:left top;transform-origin:left top;-webkit-animation:sweep-in .5s ease-in-out;animation:sweep-in .5s ease-in-out;-webkit-transform:scaleY(1);transform:scaleY(1)}@-webkit-keyframes sweep-in{from{-webkit-transform:scaleY(0);transform:scaleY(0)}to{-webkit-transform:scaleY(1);transform:scaleY(1)}}@keyframes sweep-in{from{-webkit-transform:scaleY(0);transform:scaleY(0)}to{-webkit-transform:scaleY(1);transform:scaleY(1)}}

.team-member-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:2em;text-align:center;margin:3em 0}.team-member{display:flex;flex-direction:column}.team-member-hr{width:30%;position:relative;border-top:3px solid #ffcd33;margin:auto;margin-top:1em}.team-member-name{margin-top:1em}.team-member-position{margin-top:.5em;margin-bottom:2em;font-size:.75rem}.team-member .fa.fa-linkedin{color:#000;font-size:2em}

